<template>
<div id="app">
    <!-- 内容 -->
    <el-container direction="vertical">
        <!-- 选项卡 -->
        <bk-tab :active-name="tabName" @tab-changed="changeTabPanel">
            <!-- 流程选项卡 -->
            <bk-tabpanel name="flow" :title="i18n.flow">
                <div class="p20">{{i18n.flowName}}</div>
                <el-col :span="24">
                    <div class="grid-content bg-purple-dark">
                        <div class="block">
                            <el-col :span="23" :offset="1">
                                <!-- 时间选择器 -->
                                <el-col :span="8" class="analysis-date-picker">
                                    {{i18n.startAndEndTime}}
                                    <el-date-picker :clearable="false" v-model="time" @change="plotlyDataByCategory()" type="daterange" align="center" format="yyyy-MM-dd" value-format="timestamp" :picker-options="pickerOptions">
                                    </el-date-picker>
                                </el-col>
                                <!-- 时间选择器end -->

                                <!-- 业务选择器 -->
                                <el-col :span="5" class="analysis-select">
                                    {{i18n.businessName}}
                                    <el-select clearable filterable :placeholder="i18n.choice" @change="plotlyDataByCategory()" v-model="biz_cc_id">
                                        <el-option v-for="item in bizList" :key="item.cc_id"  :label="item.cc_name" :value="item.cc_id">
                                        </el-option>
                                    </el-select>
                                </el-col>
                                <!-- 业务选择器end -->
                            </el-col>
                        </div>
                        <!-- plotly图表 -->
                        <el-col :span="22">
                            <div class="block analysis-plotly-class" id="flowBar" v-if="flowShow"></div>
                            <NoData class="empty-data" :style="{height: noDataHeight}" v-else/>
                        </el-col>
                        <!-- plotly图表end -->
                    </div>
                </el-col>
            </bk-tabpanel>
            <!-- 流程选项卡end -->

            <!-- 占比选项卡 -->
            <bk-tabpanel name="prop" :title="i18n.prop">
                <div class="p20">{{i18n.propName}}</div>
                <el-col :span="24">
                <div class="grid-content bg-purple-dark">
                    <div class="block">
                        <el-col :span="23" :offset="1">
                            <!-- 时间选择器 -->
                            <el-col :span="8" class="analysis-date-picker">
                                {{i18n.startAndEndTime}}
                                <el-date-picker :clearable="false" v-model="time" @change="plotlyDataByBizCCId()" align="center" type="daterange" format="yyyy-MM-dd" value-format="timestamp" :picker-options="pickerOptions">
                                </el-date-picker>
                            </el-col>
                            <!-- 时间选择器end -->

                            <!-- 分组选择器 -->
                            <el-col :span="5" class="analysis-select">
                                {{i18n.category}}
                                <el-select clearable filterable :placeholder="i18n.choice" @change="plotlyDataByBizCCId()" v-model="category">
                                    <el-option v-for="item in categorys" :key="item.value"  :label="item.name" :value="item.value">
                                    </el-option>
                                </el-select>
                            </el-col>
                            <!-- 分组选择器end -->
                        </el-col>
                    </div>
                     <!-- plotly图表 -->
                    <el-col :span="22">
                        <div class="block analysis-plotly-class" id="propBar" v-if="propShow" ></div>
                        <NoData class="empty-data" :style="{height: noDataHeight}" v-else/>
                    </el-col>
                    <!-- plotly图表end -->
                </div>
                </el-col>
            </bk-tabpanel>
            <!-- 占比选项卡end -->

            <!-- 节点选项卡 -->
            <bk-tabpanel name="node" :title="i18n.node">
                <div class="p20">{{i18n.nodeName}}</div>
                <el-col :span="24">
                    <div class="grid-content bg-purple-dark">
                        <div class="block">
                            <el-col :span="23" :offset="1">
                                <!-- 时间选择器 -->
                                <el-col :span="8" class="analysis-date-picker">
                                    {{i18n.startAndEndTime}}
                                    <el-date-picker :clearable="false" v-model="time" @change="dataTableDataByNode()" align="center" type="daterange" format="yyyy-MM-dd" value-format="timestamp" :picker-options="pickerOptions">
                                    </el-date-picker>
                                </el-col>
                                <!-- 时间选择器end -->

                                <!-- 业务选择器 -->
                                <el-col :span="5" class="analysis-select">
                                    {{i18n.businessName}}
                                    <el-select clearable filterable :placeholder="i18n.choice" @change="dataTableDataByNode()" v-model="biz_cc_id">
                                        <el-option v-for="item in bizList" :key="item.cc_id"  :label="item.cc_name" :value="item.cc_id">
                                        </el-option>
                                    </el-select>
                                </el-col>
                                <!-- 业务选择器end -->

                                <!-- 分组选择器 -->
                                <el-col :span="5" class="analysis-select">
                                    {{i18n.category}}
                                    <el-select clearable filterable :placeholder="i18n.choice" @change="dataTableDataByNode()" v-model="category">
                                        <el-option v-for="item in categorys" :key="item.value"  :label="item.name" :value="item.value">
                                        </el-option>
                                    </el-select>
                                </el-col>
                                 <!-- 分组选择器end -->
                            </el-col>
                        </div>
                        <!-- 表格 -->
                        <el-col :span="22" :offset="1" class="analysis-dataTable">
                            <data-table-pagination
                            :data="nodeData"
                            :total="nodeTotal"
                            :otherHeight="otherHeight"
                            :pagination="nodePagination"
                            :options="dataTableOptions"
                            :columns="nodeColumns"
                            :operates="nodeOperates"
                            @handleSortChange = "nodeHandleSortChange"
                            @handleSizeChange="nodeHandleSizeChange"
                            @handleIndexChange="nodeHandleIndexChange">
                            </data-table-pagination>
                        </el-col>
                        <!-- 表格end -->
                    </div>
                </el-col>
            </bk-tabpanel>
            <!-- 节点选项卡end -->

            <!-- 引用选项卡 -->
            <bk-tabpanel name="cite" :title="i18n.cite">
                <div class="p20">{{i18n.citeName}}</div>
                <el-col :span="24">
                    <div class="grid-content bg-purple-dark">
                        <div class="block">
                            <el-col :span="23" :offset="1">
                                <!-- 时间选择器 -->
                                <el-col :span="8" class="analysis-date-picker">
                                    {{i18n.startAndEndTime}}
                                    <el-date-picker :clearable="false" v-model="time" @change="dataTableDataByCite()" align="center" type="daterange" format="yyyy-MM-dd" value-format="timestamp" :picker-options="pickerOptions">
                                    </el-date-picker>
                                </el-col>
                                <!-- 时间选择器end -->

                                <!-- 业务选择器 -->
                                <el-col :span="5" class="analysis-select">
                                    {{i18n.businessName}}
                                    <el-select clearable filterable :placeholder="i18n.choice" @change="dataTableDataByCite()" v-model="biz_cc_id">
                                        <el-option v-for="item in bizList" :key="item.cc_id"  :label="item.cc_name" :value="item.cc_id">
                                        </el-option>
                                    </el-select>
                                </el-col>
                                <!-- 业务选择器end -->

                                <!-- 分组选择器 -->
                                <el-col :span="5" class="analysis-select">
                                    {{i18n.category}}
                                    <el-select clearable filterable :placeholder="i18n.choice" @change="dataTableDataByCite()" v-model="category">
                                        <el-option v-for="item in categorys" :key="item.value"  :label="item.name" :value="item.value">
                                        </el-option>
                                    </el-select>
                                </el-col>
                                 <!-- 分组选择器end -->
                            </el-col>
                        </div>
                        <!-- 表格 -->
                        <el-col :span="22" :offset="1" class="analysis-dataTable">
                            <data-table-pagination
                            :data="citeData"
                            :total="citeTotal"
                            :otherHeight="otherHeight"
                            :pagination="citePagination"
                            :options="dataTableOptions"
                            :columns="citeColumns"
                            :operates="citeOperates"
                            @handleSortChange = "citeHandleSortChange"
                            @handleSizeChange="citeHandleSizeChange"
                            @handleIndexChange="citeHandleIndexChange">
                            </data-table-pagination>
                        </el-col>
                        <!-- 表格end -->
                    </div>
                </el-col>
            </bk-tabpanel>
            <!-- 引用选项卡end -->

        </bk-tab>
        <!-- 选项卡end -->
    </el-container>
    <!-- 内容end -->
</div>
</template>
<script>
import '@/utils/i18n.js'
import {mapActions, mapState} from 'vuex'
import DataTablePagination from '@/components/common/dataTable/DataTablePagination.vue'
import NoData from '@/components/common/base/NoData.vue'
import {AnalysisMixins} from '@/assets/js/analysisMixins.js'

export default {
    name: 'AnalysisTemplate',
    components: {
        NoData,
        DataTablePagination
    },
    mixins: [AnalysisMixins],
    data () {
        return {
            i18n: {
                flowName: gettext('流程模板分类统计数据'),
                startAndEndTime: gettext('流程创建时间：'),
                propName: gettext('流程模板按业务维度统计数据'),
                nodeName: gettext('各流程模板中原子节点、子流程节点、网关节点个数'),
                citeName: gettext('各流程模板被引用为子流程次数、创建轻应用个数、创建任务个数'),
                businessName: gettext('业务：'),
                node: gettext('流程详情'),
                flow: gettext('流程分类'),
                prop: gettext('所属业务'),
                cite: gettext('流程引用'),
                choice: gettext('请选择'),
                category: gettext('分类：')
            },
            appType: 'app',
            category: '',
            biz_cc_id: '',
            nodeData: [],
            citeData: [],
            time: [0, 0],
            otherHeight: 208, // 除了table表格之外的高度，为了做table表格的高度自适应
            nodeTotal: 0, // table数据总条数,
            nodePageIndex: 1, // 当前页码
            nodeLimit: 15, // 每页数量
            nodePagination: {
                // 分页操作
                limit: this.nodeLimit,
                pageIndex: this.nodePageIndex,
                pageArray: this.dataTablePageArray // 公共js文件获取
            }, // 分页操作
            nodeColumns: [
                {
                    prop: 'businessName', // 识别id
                    label: gettext('所属业务'), // 表头显示名称
                    align: 'center', // 对其格式，可选（right，left，center）
                    minWidth: 150
                },
                {
                    prop: 'templateName',
                    label: gettext('流程名称'),
                    align: 'center',
                    minWidth: 285
                },
                {
                    prop: 'createTime',
                    label: gettext('创建时间'),
                    align: 'center',
                    width: 215
                },
                {
                    prop: 'creator',
                    label: gettext('创建人'),
                    align: 'center',
                    width: 130
                },
                {
                    prop: 'category',
                    label: gettext('分类'),
                    align: 'center',
                    width: 115
                },
                {
                    prop: 'atomTotal',
                    label: gettext('原子个数'),
                    align: 'center',
                    sortable: 'custom',
                    width: 155
                },
                {
                    prop: 'subprocessTotal',
                    label: gettext('子流程个数'),
                    align: 'center',
                    sortable: 'custom',
                    width: 190
                },
                {
                    prop: 'gatewaysTotal',
                    label: gettext('网关个数'),
                    align: 'center',
                    sortable: 'custom',
                    width: 170
                }
            ],
            nodeOperates: {
                width: 160,
                flex: '0 0 45%', // 按钮间距
                isShow: true, // 是否显示操作按钮
                data: [
                    {
                        label: gettext('查看'), // 按钮显示文字
                        show: true, // 是否展示该按钮
                        cls: 'bk-button bk-primary btn-size-mini', // 额外按钮样式
                        method: (index, row) => {
                            // 按钮回调方法
                            this.onTemplateHandleView(index, row)
                        }
                    },
                    {
                        label: gettext('执行历史'), // 按钮显示文字
                        show: true, // 是否展示该按钮
                        cls: 'bk-button btn-size-mini',
                        method: (index, row) => {
                            // 回调方法
                            this.onInstanceHandleView(index, row)
                        }
                    }
                ]
            },
            citeTotal: 0,
            citePageIndex: 1,
            citeLimit: 15,
            citePagination: {
                limit: this.citeLimit,
                pageIndex: this.citePageIndex,
                pageArray: this.dataTablePageArray // 公共js文件获取
            },
            citeColumns: [
                {
                    prop: 'templateId',
                    label: 'ID',
                    align: 'center',
                    width: 100
                },
                {
                    prop: 'templateName',
                    label: gettext('流程名称'),
                    align: 'center',
                    minWidth: 285
                },
                {
                    prop: 'appmakerTotal',
                    label: gettext('创建轻应用个数'),
                    align: 'center',
                    sortable: 'custom',
                    width: 270
                },
                {
                    prop: 'relationshipTotal',
                    label: gettext('被引用为子流程个数'),
                    align: 'center',
                    sortable: 'custom',
                    width: 300
                },
                {
                    prop: 'instanceTotal',
                    label: gettext('创建任务实例个数'),
                    align: 'center',
                    sortable: 'custom',
                    width: 230
                }
            ],
            citeOperates: {
                isShow: false // 是否显示操作按钮
            },
            flowShow: true,
            propShow: true,
            noDataHeight: '600px',
            nodeOrderBy: '-templateId', // 默认为id
            citeOrderBy: '-templateId',
            tabName: 'flow'
        }
    },
    created () {
        // 设置默认为30天前的查询
        let date = new Date()
        date.setHours(0, 0, 0)
        this.time[1] = Date.parse(date)
        date.setTime(date.getTime() - 3600 * 1000 * 24 * 30)
        this.time[0] = Date.parse(date)
    },
    mounted () {
        this.plotlyDataByCategory()
        // 未有初始数据时调用
        if (this.categorys.length === 0) {
            this.getCategorys()
        }
        if (this.bizList.length === 0) {
            this.getBizList()
        }
    },
    computed: {
        ...mapState({
            bizList: state => state.bizList,
            categorys: state => state.categorys,
            site_url: state => state.site_url
        })
    },
    methods: {
        ...mapActions('template/', [
            'queryTemplateData'
        ]),
        ...mapActions([
            'getBizList',
            'getCategorys'
        ]),
        // 切换每页显示的数量
        nodeHandleSizeChange (limit) {
            this.nodePageIndex = 1
            this.nodeLimit = limit
            // 根据 pageSize 和 limit 进行请求发送
            this.dataTableDataByNode()
        },
        // 切换页码
        nodeHandleIndexChange (pageIndex) {
            this.nodePageIndex = pageIndex
            // 根据 pageSize 和 limit 进行请求发送
            this.dataTableDataByNode()
        },
        nodeHandleSortChange (column, prop, order) {
            order = column[0].order === 'ascending' ? '' : '-'
            this.nodeOrderBy = column[0].prop ?  order + column[0].prop : '-templateId'
            this.dataTableDataByNode()
        },
        citeHandleSizeChange (limit) {
            this.citePageIndex = 1
            this.citeLimit = limit
            this.dataTableDataByCite()
        },
        citeHandleIndexChange (pageIndex) {
            this.citePageIndex = pageIndex
            this.dataTableDataByCite()
        },
        // 排序
        citeHandleSortChange (column, prop, order) {
            order = column[0].order === 'ascending' ? '' : '-'
            this.citeOrderBy = column[0].prop ?  order + column[0].prop : '-templateId'
            this.dataTableDataByCite()
        },
        // 查看
        onTemplateHandleView (index, row) {
            window.open(this.site_url + 'template/edit/' + row.businessId + '/?template_id=' + row.templateId)
        },
        onInstanceHandleView (index, row) {
            window.open(this.site_url + 'taskflow/home/' + row.businessId + '/?template_id=' + row.templateId)
        },
        // 切换Tab事件
        changeTabPanel (name) {
            this.tabName = name
            switch (name) {
                case 'flow' :
                    this.plotlyDataByCategory()
                    break
                case 'prop' :
                    this.plotlyDataByBizCCId()
                    // 清除图表内容
                    this.clearPlot('flowBar')
                    break
                case 'node' :
                    this.dataTableDataByNode()
                    break
                case 'cite' :
                    this.dataTableDataByCite()
                    break
            }
        },
        async queryPlotlyData (data, id, title) {
            const templateData = await this.queryTemplateData(data)
            let total = templateData.data.total
            if (id === 'propBar' && total === 0) {
                this.propShow = false
                return
            }
            else if (id === 'propBar') {
                this.propShow = true
                this.$nextTick(function () {
                    this.createPolt(id, total, templateData.data.groups, title)
                    var myPlot = document.getElementById(id)
                    var vueThis = this
                    myPlot.on('plotly_click', function (data) {
                        let point = data.points[0]
                        let id = point.data.id[point.pointIndex]
                        vueThis.tabName = 'node'
                        vueThis.biz_cc_id = id
                        vueThis.nodePageIndex = 1
                        vueThis.nodeLimit = 15
                        vueThis.dataTableDataByNode()
                    })
                })
            }
            else if (id === 'flowBar' && total === 0) {
                this.flowShow = false
                return
            }
            else {
                this.flowShow = true
                this.$nextTick(function () {
                    this.createPolt(id, total, templateData.data.groups, title)
                    var myPlot = document.getElementById(id)
                    var vueThis = this
                    myPlot.on('plotly_click', function (data) {
                        let point = data.points[0]
                        let id = point.data.id[point.pointIndex]
                        vueThis.tabName = 'node'
                        vueThis.category = id
                        vueThis.nodePageIndex = 1
                        vueThis.nodeLimit = 15
                        vueThis.dataTableDataByNode()
                    })
                })
            }
        },
        plotlyDataByCategory () {
            let time = this.getUTCTime(this.time)
            const data = {
                group_by: 'category',
                conditions: JSON.stringify({
                    create_time: time[0],
                    finish_time: time[1],
                    biz_cc_id: this.biz_cc_id
                })
            }
            this.queryPlotlyData(data, 'flowBar', gettext('流程模板个数和占比'))
        },
        plotlyDataByBizCCId () {
            let time = this.getUTCTime(this.time)
            const data = {
                group_by: 'biz_cc_id',
                conditions: JSON.stringify({
                    create_time: time[0],
                    finish_time: time[1],
                    category: this.category
                })
            }
            this.queryPlotlyData(data, 'propBar', gettext('流程模板个数和占比'))
        },
        async queryDataTableData (data) {
            const templateData = await this.queryTemplateData(data)
            switch (data.group_by) {
                case 'template_node':
                    this.nodeData = templateData.data.groups
                    this.nodeTotal = templateData.data.total
                    break
                case 'template_cite':
                    this.citeData = templateData.data.groups
                    this.citeTotal = templateData.data.total
                    break
            }
        },
        dataTableDataByNode () {
            let time = this.getUTCTime(this.time)
            const data = {
                group_by: 'template_node',
                conditions: JSON.stringify({
                    create_time: time[0],
                    finish_time: time[1],
                    biz_cc_id: this.biz_cc_id,
                    category: this.category,
                    order_by: this.nodeOrderBy
                }),
                pageIndex: this.nodePageIndex,
                limit: this.nodeLimit
            }
            this.queryDataTableData(data)
        },
        dataTableDataByCite () {
            let time = this.getUTCTime(this.time)
            const data = {
                group_by: 'template_cite',
                conditions: JSON.stringify({
                    create_time: time[0],
                    finish_time: time[1],
                    biz_cc_id: this.biz_cc_id,
                    category: this.category,
                    order_by: this.citeOrderBy
                }),
                pageIndex: this.citePageIndex,
                limit: this.citeLimit
            }
            this.queryDataTableData(data)
        }
    }
}
</script>

<style lang="scss">
    @import '@/scss/analysis.scss';
</style>